.dropup, .dropdown {
  position: relative;
}

.dropdown-toggle:focus {
  outline: 0;
}

.dropdown-menu {
  position: absolute;
  top: 116%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  list-style: none;
  font-size: 14px;
  text-align: left;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border: 1px solid color(red);
  border-radius: 4px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background-clip: padding-box;
}

.dropdown-menu.pull-right {
  right: 0;
  left: auto;
}

.dropdown-menu .divider {
  height: 1px;
  margin: 9px 0;
  overflow: hidden;
  background-color: #e5e5e5;
}

.dropdown-menu > li > a {
  display: block;
  padding: 3px 20px;
  clear: both;
  font-weight: normal;
  line-height: 1.42857143;
  color: #333333;
  white-space: nowrap;
}

.dropdown-menu > li > a:hover, .dropdown-menu > li > a:focus {
  text-decoration: none;
  color: #262626;
  background-color: #f5f5f5;
}

.dropdown-menu > .active > a {
  color: #ffffff;
  text-decoration: none;
  outline: 0;
  background-color: #337ab7;
}

.dropdown-menu > .active > a:hover, .dropdown-menu > .active > a:focus {
  color: #ffffff;
  text-decoration: none;
  outline: 0;
  background-color: #337ab7;
}

.dropdown-menu > .disabled > a {
  color: #777777;
}

.dropdown-menu > .disabled > a:hover, .dropdown-menu > .disabled > a:focus {
  color: #777777;
}

.dropdown-menu > .disabled > a:hover, .dropdown-menu > .disabled > a:focus {
  text-decoration: none;
  background-color: transparent;
  background-image: none;
  filter: progid:DXImageTransform.Microsoft.gradient(enabled = false);
  cursor: not-allowed;
}

.open > .dropdown-menu {
  display: block;
}

.open > a {
  outline: 0;
}

.dropdown-menu-right {
  left: auto;
  right: 0;
}

.dropdown-menu-left {
  left: 0;
  right: auto;
}

.dropdown-header {
  display: block;
  padding: 3px 20px;
  font-size: 12px;
  line-height: 1.42857143;
  color: #777777;
  white-space: nowrap;
}

.dropdown-backdrop {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 990;
}

.pull-right > .dropdown-menu {
  right: 0;
  left: auto;
}

.dropup .caret, .navbar-fixed-bottom .dropdown .caret {
  border-top: 0;
  border-bottom: 4px dashed;
  border-bottom: 4px solid \9;
  content: "";
}

.dropup .dropdown-menu, .navbar-fixed-bottom .dropdown .dropdown-menu {
  top: auto;
  bottom: 100%;
  margin-bottom: 2px;
}

@media (min-width: 768px) {
  .navbar-right .dropdown-menu {
    left: auto;
    right: 0;
  }
  .navbar-right .dropdown-menu-left {
    left: 0;
    right: auto;
  }
}

.dropdown-user {
  margin: 0px;
  padding: 0px 4px;
  height: 46px;
  display: inline-block;
  position: relative;
}

.dropdown-user .dropdown-menu {
  background-color: color(red);
  border-radius: 0px;
}

.dropdown-user .dropdown-menu:before {
  position: absolute;
  top: -7px;
  right: 9px;
  display: inline-block !important;
  border-right: 7px solid transparent;
  border-bottom: 7px solid color(red);
  border-left: 7px solid transparent;
  border-bottom-color: color(red);
  content: '';
}

.dropdown-user .dropdown-menu li:last-child a {
  border-bottom: none;
}

.dropdown-user .dropdown-menu li a {
  display: block;
  padding: 5px 14px;
  clear: both;
  font-weight: 300;
  line-height: 1.42857143;
  color: #fff;
  white-space: nowrap;
  text-decoration: none;
  font-size: 13px;
  border-bottom: 1px solid #f2f2f2;
  margin: 0px 16px;
}

.dropdown-user .dropdown-menu li a i {
  margin-right: 10px;
}

.dropdown-user .dropdown-menu li a:hover {
  background-color: color(rojo-claro);
}

.dropdown-user a.dropdown-toggle {
  padding: 10px 6px 12px 8px;
  display: block;
  position: relative;
  text-decoration: none;
  font-size: 0.8rem;
  line-height: 1.5rem;
  vertical-align: middle;
}

.dropdown-user a.dropdown-toggle .username {
  color: #fff;
  text-decoration: none;
  border: none;
  vertical-align: middle;
  padding: 0px;
  margin: 0px;
}

.dropdown-user a.dropdown-toggle img {
  float: right;
  margin-top: -2px;
  margin-left: 10px;
  height: 29px;
  display: inline-block;
}
